<template>
  <div style="font-size: 14px; line-height: 22px; margin-bottom: 7px; color: rgba(0, 0, 0, 0.65);">
    <p style="margin-right: 8px; display: inline-block; color: rgba(0, 0, 0, 0.85);">{{title}}</p>
    {{ content }}
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: null
    },
    content: {
      type: String,
      default: null
    }
  }
};
</script>

<style>
</style>


